:root {
  --primary-color:  salmon;
  --header-bg-color: #1c1c1c;
  --main-bg-color: #1c1817;
}


.App {
  text-align: center;
  background-color: var(--main-bg-color);
  min-height: 100vh;
  color: white;
}



.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: var(--header-bg-color);
  min-height: 40vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  position: relative;
}

.App-body {
  position: relative;
  /* background: linear-gradient(180deg, var(--primary-color) -74%, transparent 14%); */
  padding-top: 150px;
}

.App-link, a {
  color: var(--primary-color);
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.footer {
  font-size: 12px;
}



.work-list {
  padding-left: 0;
  list-style-type: none;
}

.work-list li {
  display: inline-block;
  margin-right: 30px;
  margin-bottom: 10px;

}

.work-list li a {
  color: white;
  text-decoration: none;

  padding: 10px;

  background: linear-gradient(to left, transparent 51%, var(--primary-color) 50%) right;
  background-size: 200%;
  transition: .3s ease-out;
}

.work-list li a:hover {
  background-position: left;
}

.portfolio-list {
  text-align: center;
  padding-left: 0;
  list-style-type: none;
}

.portfolio-list li {
  display: inline-block;
  height: 50px;
  width: 50px;
  margin-right: 50px;
}

.portfolio-list li a {
  display: block;
}

.container {
  margin-bottom: 150px;
}

.social-icon svg {
  transition: 0.3s;
  transform: scale(1);
}

.social-icon:not(:last-child) {
  margin-right: 20px;
}

.social-icon:hover svg {
  transform: scale(1.5);
  fill: white;
}

.gradient-bg {
  position: absolute;
  width: 100%;
  height: 100%;
  bottom: 0;
  z-index: 1;
  background: linear-gradient(360deg, var(--primary-color) -100%, transparent 29%);
}
.gradient-bg.down {
  top: 0;
  background: linear-gradient(180deg, var(--primary-color) -74%, transparent 14%);
}

.main-app-loader {
  position: absolute;
  top: 40vh;
  width: 100%;
}

.body-content, .header-content {
  z-index: 2;
  opacity: 0;
  position: relative;
  overflow-y: hidden;
}

.header-image {
  max-width: 150px;
  border-radius: 50%;
}

 /* width */
 ::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: var(--primary-color);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: var(--primary-color);
} 